import React from 'react';
import {useNavigate} from 'react-router-dom';
const Top = ({targetDt}) => {
    const navigate = useNavigate();
   
    const onDateChange = (e)=>{
        e.preventDefault();
        navigate('/' + e.currentTarget.value)
    }
    return (
        <div>
            <h1> </h1>
            <form>
                <input type='date' onChange={onDateChange} defaultValue={targetDt} />
            </form>
        </div>
    );
};

export default Top;